<template>
    <div class="userRegister">
        <p class="regLogin">如有账号，可<el-button type="text">立即登录</el-button></p>
        <el-form :model="loginInfo" @keyup.enter.native="submitForm('loginInfo')" :rules="loginRules" class="demo-ruleForm" ref="loginInfo" :label-position="labelPostion" label-width="100px">
            <div class="regForm">
                <h1>账号设置<span>请设置个人信息与手机号登录</span></h1>
                <el-form-item label="身份类型" prop="identityType">
                    <el-select v-model="loginInfo.identityType" clearable placeholder="请选择">
                        <el-option
                        v-for="item in options"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="姓名" prop="name">
                    <el-input size="medium" v-model="loginInfo.name" placeholder="姓名"></el-input>
                </el-form-item>
                <el-form-item label="证件类型" prop="papersType">
                    <el-select v-model="loginInfo.papersType" clearable placeholder="请选择">
                        <el-option
                        v-for="item in cardOptions"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="证件号码" prop="papersNum">
                    <el-input size="medium" v-model="loginInfo.papersNum" placeholder="证件号码"></el-input>
                </el-form-item>
                <el-form-item label="手机号" prop="loginName">
                    <el-input size="medium" v-model="loginInfo.loginName" placeholder="手机号/账号"></el-input>
                </el-form-item>
                <el-form-item label="验证码" prop="code">
                    <el-input size="medium" prop="code" v-model="loginInfo.code" placeholder="验证码"></el-input>
                    <button size="medium" @click="getCode(loginInfo)" class="code-btn" :disabled="!show">
                        <span v-show="show">发送验证码</span>
                        <span v-show="!show" class="count">{{count}} s</span>
                    </button>
                </el-form-item>
                <el-form-item label="密码" prop="password">
                    <el-input size="medium" prefix-icon="el-icon-lock" type="password" v-model="loginInfo.password" placeholder="密码"></el-input>
                </el-form-item>
                <el-form-item label="用户类型" prop="userType">
                    <!-- <el-input size="medium" prefix-icon="el-icon-lock" type="password" v-model="loginInfo.password" placeholder="密码"></el-input> -->
                    <el-select v-model="loginInfo.userType" clearable placeholder="请选择">
                        <el-option
                        v-for="item in userOptions"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                        </el-option>
                    </el-select>
                </el-form-item>
                <div class="company">
                    <h1>单位信息<span>请填写单位信息</span></h1>
                    <el-form-item label="单位名称" prop="company">
                        <el-input size="medium" type="text" v-model="loginInfo.company" placeholder="手机号/账号"></el-input>
                    </el-form-item>
                    <el-form-item label="科室" prop="office">
                        <el-input size="medium" type="text" v-model="loginInfo.office" placeholder="密码"></el-input>
                    </el-form-item>
                    <el-form-item label="职务" prop="duty">
                        <el-input size="medium" type="text" v-model="loginInfo.duty" placeholder="手机号/账号"></el-input>
                    </el-form-item>
                </div>
                <el-button type="primary" style="width:100%;" @click="submitForm('loginInfo')">立即注册</el-button>
            </div>
        </el-form>
    </div>
</template>
<script>
import {getCodes, getUserRegister, getUserLogin} from '@/request'
const TIME_COUNT = 60 // 倒计时的时间
export default {
    data() {
        var funChina = (rule, value, callback) => {
            if (!value) {
                return callback(new Error('不可为空'))
            } else {
                const regs = /^[0-9a-zA-Z]+$/
                if (regs.test(value)) {
                    callback()
                } else {
                    return callback(new Error('账号只可为数字与字母'))
                }
            }
        }
        return{
            // 注册
            loginInfo: {},
            loginRules: {
                loginName: [
                    {required: true, trigger: ['blur', 'change'], validator: funChina},
                    {max: 11, message: '密码长度最少为11位', trigger: ['blur', 'change']}
                ],
                password: [
                    {required: true, message: '请输入密码', trigger: ['blur', 'change']},
                    {min: 5, message: '密码长度最少为6位', trigger: ['blur', 'change']}
                ],
                identityType: [
                    {required: true, message: '不可为空', trigger: ['blur', 'change']}
                ],
                name: [
                    {required: true, message: '不可为空', trigger: ['blur', 'change']}
                ],
                papersType: [
                    {required: true, message: '不可为空', trigger: ['blur', 'change']}
                ],
                code: [
                    {required: true, message: '不可为空', trigger: ['blur', 'change']}
                ],
                userType: [
                    {required: true, message: '不可为空', trigger: ['blur', 'change']}
                ],
                papersNum: [
                    {required: true, message: '不可为空', trigger: ['blur', 'change']}
                ],
                company: [
                    {required: true, message: '不可为空', trigger: ['blur', 'change']}
                ],
                office: [
                    {required: true, message: '不可为空', trigger: ['blur', 'change']}
                ],
                duty: [
                    {required: true, message: '不可为空', trigger: ['blur', 'change']}
                ],
            },
            // 验证码
            show: true,
            count: '',
            timer: null,
            labelPostion: 'right',
            options:[
                {
                    value: '1',
                    label: '团员'
                }, {
                    value: '2',
                    label: '党员'
                }, {
                    value: '3',
                    label: '群众'
                }
            ],
            cardOptions: [
                {
                    value: '1',
                    label: '身份证'
                }, {
                    value: '2',
                    label: '驾驶证'
                }
            ],
            userOptions: [
                {
                    value: '1',
                    label: '申请人'
                }, {
                    value: '2',
                    label: '职业人士'
                }
            ]
        }
    },
    methods: {
        // 验证码
        getCode (formData) {
            // 验证码倒计时
            if (!this.timer) {
                this.count = TIME_COUNT
                this.show = false
                this.timer = setInterval(() => {
                    if (this.count > 0 && this.count <= TIME_COUNT) {
                        this.count--
                    } else {
                        this.show = true
                        clearInterval(this.timer)
                        this.timer = null
                    }
                }, 1000)
            }
        },
        // 注册提交
        submitForm (formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    getUserRegister(this.loginInfo).then((res) => {
                        if (res.status == '200') {
                            this.$message({
                                showClose: true,
                                message: '注册成功！',
                                type: 'success'
                            });
                            var data = res.data
                            getUserLogin({username: data.loginName,password: this.loginInfo.password,code: this.loginInfo.code}).then((res) => {
                                if (res.status == '200') {
                                    this.$store.commit('SET_MENU', res.data)
                                    this.$router.push({path: '/index'})
                                } else {
                                    this.$message({
                                        showClose: true,
                                        message: res.msg,
                                        type: 'error'
                                    });
                                }
                            })
                        } else {
                            this.$message({
                                showClose: true,
                                message: res.msg,
                                type: 'error'
                            });
                        }
                    })
                }
            })
        },
    },
    mounted() {
    }
}
</script>>
<style scoped>
.userRegister{
    width: 30%;
    margin: auto;
    text-align: left
}
.regLogin{
    font-weight: 400;
    font-size: 14px;
    color: rgba(186, 185, 185, 1);
}
.regForm{
    width: 100%;
    border-color: rgb(187, 187, 187);
    border-width: 1px;
    border-style: solid;
    border-radius: 6px;
    font-size: 14px;
    padding: 20px;
    text-align: center;
    line-height: 20px;
    font-weight: normal;
    font-style: normal;
    opacity: 1;
}
.regForm h1{
    text-align: left;
}
.regForm h1 span{
    font-weight: 400;
    font-size: 14px;
    color: rgba(186, 185, 185, 1);
    margin-left: 20px
}
/* 验证码 */
.pr {
   position: relative;
}
.code-btn {
   width: 100px;
   height: 34px;
   position: absolute;
   top: 3px;
   right: 5px;
   z-index: 222;
   color: #F5A623;
   font-size: 14px;
   border: none;
   border-left: 1px solid #bababa;
   padding-left: 16px;
   background-color: #fff;
}
.forget{
    margin-bottom: 5px;
    display: flex;
    justify-content: space-between
}
.company{
    border-top: 1px solid #ccc
}
.el-select{
    width: 100%
}
</style>


